* {
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

html {
  overflow-y: scroll;
  scroll-behavior: smooth;
}

.App {
  background:#f5f5f5;
  min-height: 100vh;
  color: rgb(0, 0, 0);
}

.navbar{
  position: fixed;
  display: none;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  height: 10vh;
  background:#ffffff;
  width: 100%;
  z-index: 1;
}

.nav {
  display: flex;
  justify-content: right;
  list-style: none;
  margin-right: 15%;
  font-size: 1.2em;
  font-weight: 300;
  letter-spacing: 1px;
}

.logo {
  flex: 1 1 auto;
  margin-left: 10%;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: bold;
  font-size: 35px;
}

a{
  margin: 15px;
  color:rgb(54, 54, 54);
  text-decoration: none;
  text-transform: uppercase;
}

a:hover{
  color:rgb(117, 117, 117);
}

.home-section, .about-section, .projects-section, .contact-section {
  position:static;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
  width:100%;
  height: 100vh;
  background: rgb(26, 26, 26);
  color: white;
  overflow: hidden;
}

.text-part {
  padding:3.1vh 20vw 3.1vh 20vw;
}

h1{
  padding: 3.1vh 0vw;
  font-size: 3vw;
}

p{
  padding: 3.1vh 0vw;
  font-size: 1.1vw;
  line-height: 5vh;
}

img {
  height: 35vh;
  width: 35vh;
  padding: 3.1vh 3.1vh;
}

@media only screen and (min-width: 1000px) {
  .navbar{
    display: flex;
  }
}